import React from 'react'
import { useState } from 'react'

export default function TextForm(props) {
    const [state, setstate] = useState("");
    const toUpperCase = ()=> {
        var newtext=state.toUpperCase();
        setstate(newtext);
        props.showAlert("Converted to Upper Case");
    }
    const toLowerCase = ()=> {
        var newtext=state.toLowerCase();
        setstate(newtext);
        props.showAlert("Converted to Lower Case");
    }
    const handleOnChange = (event)=> {
        setstate(event.target.value);
    }
    const copyToClipBoard = () => {
        navigator.clipboard.writeText(state);
        props.showAlert("Text copied to Clip Board");
    }
    return (
        <>
            <div>
                <h1 style={{color: props.mode==="light"?"black":"white"}}>{props.heading}</h1>
                <div className="mb-3 my-4">
                    <textarea className="form-control txtform" placeholder="Enter any text here." value={state} onChange={handleOnChange} id="exampleFormControlTextarea1" rows="6"></textarea>
                </div>
                <button className="btn btn-success my-2 mx-2" onClick={toUpperCase}>Change to Uppercase</button>
                <button className="btn btn-success mx-2" onClick={toLowerCase}>Change to Lowercase</button>
                <button className="btn btn-success mx-2" data-clipboard-target="#myBox" onClick={copyToClipBoard}>Copy Text</button>
            </div>
            <div className="container">
                <h4 className="my-2" style={{color: props.mode==="light"?"black":"white"}}>  Textlyzed Results : </h4>
                <p style={{color: props.mode==="light"?"black":"white"}} className="resultline">Your text has <span className="results">{state===""?0:state.split(" ").filter((elem)=>{return elem.length!==0}).length}</span> words and <span className="results">{state.length-state.split(" ").length+1}</span> characters.</p>
            </div>
        </>
    )
}